import React, { Component } from 'react';

class Phone extends Component {
    render() {
        return (
        <div id="phoneContact" className="phoneContact row">
            <div className="container">
                <h2>Свържете се с нас на телефон: +359 889 11 77 42</h2>
            </div>
        </div>
        );
    }
}

export default Phone;



