import React from 'react';

const Top = () => (
    <div className="parallaxTop">
        <div className="jumboBanner shadow">
            <h1 className="banner">Учебен център "Синергия Плюс"</h1>
            <h3 className="motto">
                <p>"Помагаме на хората да инвестират в бъдещето си. А ти помисли ли за своето?"</p>
                <p>"По-скъпо от времето няма! Инвестирай в своето бъдеще, инвестирай в образованието си!"</p>
            </h3>
            <button id="bannerBtn" className="bannerBtn">Научете повече</button>
        </div>
    </div>
);

export default Top;




